.resume{
    height: 100%;
    z-index: -1;
}

.timeline{
    display: flex;
    width: 100%;
    padding: 20px;

    &_experience, &_education{
        width: 50%;
        gap : 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;

        &_header{
                display: inline-block;
                color : var(--yellow-theme-sub-text-color);
                font-size: 1.8rem;
                font-weight: 700;
                line-height: 1rem;
                letter-spacing: 0.5px;
                position: relative;
                text-transform: uppercase;
                padding: 5px 10px;
                border-bottom: 2px solid var(--yellow-theme-main-color);
        }

        &_header::before, &_header::after{
            position: absolute;
            content :'';
            width: 2px;
            height: 5px;
            background-color: var(--yellow-theme-main-color);
            bottom: -1px;
        }

        &_header::before{
            left : 0;
        }

        &_header::after{
            right: 0;
        }

        &_vte{
            padding: 0 0 30px 0;

            &:last-child{
                padding-bottom: 0px;
            }
        }

        .vte-title-wrapper{
            
            
            align-items: center;
            

            h3, h4{
                margin-top: 10px;
                font-size: 16px;
            }

            h3{
                font-size: 20px ;
                margin-bottom: 20px;
            }

            
        }

        .vte-title-wrapper-description{
            font-size: 18px;

        }

        .vertical-timeline-element-content{
            box-shadow: none;
        }

        .vertical-timeline-element-content-arrow{
            display: none;
        }

        .vertical-timeline-element-date{
            opacity : 1;
            color : var(--yellow-theme-main-color);
            font-size: 1px;
        }
    }
}

@media only screen and (max-width:768px){
    .timeline{
        flex-direction: column;

        &_experience, &_education{
            width : 100%;
            gap : 2rem;

            &_header{
                margin-top: 20px;
                
            }
        }

        
    }
}