.portfolio{
    &_content{
        padding : 20px;
        width:100%;

        &_filter{
            display: inline-block;
            margin: 0 auto;
            border-radius: 25px;
            border: 1px dashed var(--yellow-theme-main-color);

            li{
                font-size: 16px;
                display: inline-block;
                cursor: pointer;
                position: relative;
                color : var(--yellow-theme-main-color);
                text-transform: uppercase;
                padding : 12px 20px;
                overFlow:hidden;
                transition: all 0.5s ease;

                &.active{
                    background: var(--yellow-theme-main-color);
                    color : var(--yellow-theme-sub-text-color);
                    font-weight: 600;
                    border-radius: 25px;
                }
            }
        }

        &_cards{
            //display: flex;
            //flex-wrap: wrap;
            display: grid;
            grid-template-columns: repeat(3, 3fr);
            grid-auto-rows: auto;
            gap: 1rem;
            margin-top: 10px;
            justify-content: space-between;

            &_item{
                height: 400px;
                cursor : pointer;
                position : relative;
                width: 100%;

                &:hover .overlay{
                    opacity: 1;
                }

                &_img{
                    height: 100%;

                    a{
                        height: 100%;

                        img{
                            height: inherit;
                            object-fit:cover;
                            width: 100%;
                        }
                    }
                }

                .overlay{
                    position: absolute;
                    top : 0;
                    bottom: 0;
                    right: 0;
                    left : 0;
                    height: 100%;
                    width: 100%;
                    opacity: 0;
                    transition: 0.5s ease;
                    background-color: var(--yellow-theme-main-color);

                    div{
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        text-align: center;

                        p{
                            color : #000;
                            margin : 0px;
                            font-size: 3.5rem;
                            position: relative;
                            overflow: hidden;
                            white-space: nowrap;
                            animation: typingEffect 1s steps(30, end) ;
                        }

                        button{
                            cursor: pointer;
                            padding : 10px 70px;
                            margin-top : 16px;
                            font-size: 2rem;
                            background: transparent;
                            color: #000;
                            border: 1.5px solid #000;
                            border-radius: 4px;
                            font-weight: 500;
                        }
                    }
                }
            }

        }
    }
}

@keyframes typingEffect {
    from{
        width: 0;
    }
    to{
        width: 100%;
    }
}

@media only screen and (max-width : 480px){
    .portfolio_content_filter > li{
        font-size: 11px;
    }

    .portfolio_content_cards {
        grid-template-columns: repeat(1,1fr);
    }

}

@media only screen and (min-width : 480px) and (max-width:767px){
    .portfolio_content_filter > li{
        font-size: 14px;
    }

    .portfolio_content_cards {
        grid-template-columns: repeat(2,1fr);

        &_item{
            .overlay{
                div>p{
                    font-size: 2.5rem;
                }
            }
        }
    }

}

@media only screen and (min-width : 768px) and (max-width:1100px){

    .portfolio_content_cards {
        grid-template-columns: repeat(2,1fr);

    }

}