.contact{

    &_content{
        padding : 20px;

        &_header-text{
            color: var(--yellow-theme-sub-text-color);
            display: inline-block;
            font-size: 22px;
            font-weight: 600;
            line-height: 1em;
            letter-spacing: 0.5px;
            position: relative;
            text-transform: uppercase;
            padding: 5px 10px;
            border-bottom: 2px solid var(--yellow-theme-main-color);
        }

        &_header-text::before, &_header-text::after{
            position: absolute;
            content: "";
            width: 2px;
            height: 5px;
            background-color: var(--yellow-theme-main-color);
            bottom: -1px;
        }

        &_header-text::before{
            left:0;
        }

        &_header-text::after{
            right:0;
        }

        &_form{
            margin-top:30px;

            &_controls{
                display: grid;
                grid-template-columns: repeat(2,1fr);
                gap: 40px;
                margin-bottom: 30px;
                

                div{
                    position: relative;

                    .inputName, .inputEmail, .inputDescription{
                        color: var(--yellow-theme-sub-text-color);
                        width: 100%;
                        border: 2px solid var(--yellow-theme-main-color);
                        padding: 16px 12px;
                        transition: all 0.5s ease;
                        position: relative;
                        background-color: var(--yellow-theme-sub-text-color);
                        background: transparent;
                    }

                    .nameLabel, .emailLabel, .descriptionLabel{
                        font-weight: 500;
                        position: absolute;
                        top:7px;
                        left: 12px;
                        transition: all 0.2s ease;
                        color: var(--yellow-theme-sub-text-color);
                        font-size: 16px;
                    }

                    .inputName:focus, .inputEmail:focus, .inputDescription:focus{
                        outline: none;
                    }

                    .inputName:focus + .nameLabel, .inputName:valid + .nameLabel,
                    .inputEmail:focus + .emailLabel, .inputEmail:valid + .emailLabel,
                    .inputDescription:focus + .descriptionLabel, .inputDescription:valid + .descriptionLabel{
                        font-size: 18px;
                        top :-25px;
                        left: 0px;
                    }

                }

                .submit-btn{

                    .submit{
                        height: 5rem;
                        width : 20rem;
                        cursor: pointer;
                        font-size: 2rem;
                        background: transparent;
                        color: var(--yellow-theme-main-color);
                        border: 1.5px solid var(--yellow-theme-main-color);
                        border-radius: 4px;
                        font-weight: 500;
                    }

                    .submit:hover{
                        color: black;
                        background: var(--yellow-theme-main-color);
                    }
                }
            }
        }

        // button{
        //     cursor: pointer;
        //     padding: 10px 70px;
        //     font-size: 2rem;
        //     background: transparent;
        //     color: var(--yellow-theme-main-color);
        //     border: 1.5px solid var(--yellow-theme-main-color);
        //     border-radius: 4px;
        //     font-weight: 500;
        // }

        // button:hover{
        //     color: black;
        //     background: var(--yellow-theme-main-color);
        // }
    }
}

@media only screen and (max-width:768px) {
    .contact_content_form{
        text-align: center;

        &_controls{
            grid-template-columns: repeat(1, 1fr);
        }
    }

}